import Vue from 'vue'
import Router from 'vue-router'
import NavarBar from '@/layout/NavarBar'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  linkExactActiveClass: 'vue-school-active-class',
  routes: [
    {
      path: '/',
      name: 'root',
      component: () => import('@/views/Admin'),
      children: [
        {
          path: '/',
          components: {
            default: NavarBar,
            content: () => import('@/views/LoginPage')
          }
        },
        {
          path: 'admin/home',
          name: 'Home',
          components: {
            default: NavarBar,
            content: () => import('@/views/Home')
          }
        },
        {
          path: 'register',
          name: 'RegisterPage',
          components: {
            default: NavarBar,
            content: () => import('@/views/RegisterPage')
          }
        },
        {
          path: 'user-board',
          name: 'ListUser',
          components: {
            default: NavarBar,
            content: () => import('@/views/ListUser')
          }
        }
      ]
    }
  ]
})

// router.beforeEach((to, from, next) => {
