import axios from 'axios'

const BASE_API_URL = 'http://localhost:8080/'

// This class contain method call API to authenticate user and sign up
class AuthService {
  login (user) {
    return axios.post(BASE_API_URL + 'api/auth/signin', {
      username: user.username,
      password: user.password
    })
      .then(response => {
        if (response.data.token) {
          localStorage.setItem('user', JSON.stringify(response.data))
        }
        console.log(JSON.stringify(response.data))
        return response.data
      })
  }

  register (user) {
    return axios.post(BASE_API_URL + 'api/auth/signup', {
      username: user.username,
      password: user.password,
      email: user.email,
      roles: user.roles
    })
      .then(response => {
        console.log('User reigister successfully', response.data.status)
      })
      .catch(error => {
        if (error.response) {
          console.log(error.response.data)
          console.log(error.response.status)
          console.log(error.response.headers)
        }
      })
  }

  logout () {
    localStorage.removeItem('user')
  }
}

export default new AuthService()



