import AuthService from '@/services/auth-service'

const user = JSON.parse(localStorage.getItem('user'))

// Depending on user be exists (not 'null' and not 'undefined') => we will create state for this module
// var initialState = user ? { status: { loggin: true }, user } : { status: {loggin: false}, user: null }

export const authStoreModule = {
  namespaced: true, // this will config all method will be nest , don use namespace global
  state: {
    status: { loggin: false },
    user: user
  },
  // thao tac voi cac state
  mutations: {
    logginSucess (state, user) {
      state.status.loggin = true
      state.user = user
    },

    logginFailed (state) {
      state.status.loggin = false
    },

    registerSuccess (state) {
      state.status.loggin = true
    },

    registerFailed (state) {
      state.status.loggin = false
    },

    logout (state) {
      state.status.loggin = false
      state.user = null
    }
  },
  actions: {
    // create function to login, signup, logout
    // then() syntax : then(function(resovle), function(reject))
    loggIn ({ commit }, user) {
      return AuthService.login(user).then(
        user => {
          commit('logginSucess', user)
          return Promise.resolve(user)
        },
        error => {
          commit('logginFailed', error)
          return Promise.reject(error)
        }
      )
    },

    signUp ({ commit }, user) {
      return AuthService.register(user)
        .then(
          function (response) {
            commit('registerSuccess')
            return Promise.resolve(response.data)
          },
          function (error) {
            commit('registerFailed')
            return Promise.reject(error)
          }
        )
    },

    logout ({commit}) {
      AuthService.logout()
      commit('logout')
    }
  }
}



